@import url(https://fonts.googleapis.com/css?family=Raleway:400,500,800);
@font-face {
    font-family: 'header';
    src: url('./StandardHeader.ttf') format('truetype');
    font-display: swap;
}
body{
    background-image: url('./ron-se-Z4pzS12TA0E-unsplash.jpg');
    background-repeat: repeat-y;
    background-blend-mode: overlay;
    object-fit: cover;
    }

/* .main {
    background-color: rgb(197, 197, 32)
} */

main {
    align-items: center;
    justify-content: center;
}

.head {
    border: 2px solid yellow;
    box-sizing: content-box;
    width: 140px;
    padding-left: 10px;
    border-radius: 1rem;
    background-color: red;
}

.bahar {
    border: 4px solid red;
    border-radius: 3em;
}

.div {
    align-items: center;
    display: flex;
    justify-items: center;
}

.container {
    max-width: 1170px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
    background-repeat: repeat;

}

a {
    font-family: 'raleway';
    font-weight: 600;
}

p {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Raleway';
    font-size: 150px;
    font-weight: 0;
}
header {
    background-color: red;
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 200px;
    position: relative;
    background-color: rgba(0, 0, 0, 0.5);
    background-blend-mode: overlay;
    color: #fff;
}

.img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    background-repeat: repeat;
    filter: 'blur(5px)';
    /*opacity: 90%;
    backdrop-filter:initial;
    box-shadow: black; */
    /* background-blend-mode:screen; */
}

#img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    background-repeat: repeat;
}

.text {
    z-index: 1;
}

.containerrr {
    height: 100vh;
    width: 100vh;
}

  /*grid layout for settings and code preview content*/
  #copy-txt-component {
    height: fit-content;
    max-width: 2000px;
    border: 1px solid var(--accent-color);
    border-radius: 25px;
    display: grid;
    grid-auto-columns: 1fr;
    grid-template-rows: 40px 1fr;
    overflow: hidden;
    /* margin-left: 20px; */
  }
  /*the small bar abox the code*/
  .settings {
    transition:1s;
    background: rgba(7, 0, 200, 0.963);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 20px;
    font-family: sans-serif;
    letter-spacing: 1px;
  }
  /*the title in the setting bar */
  .title {
    font-size: 1.2rem;
    margin-left: -15px;
    font-family: Raleway;
    /* color:rgb(254, 6, 6); */
    color: #fff;
  }
  /*the red yllow and green circles container*/
  .ryg {
    display: flex;
    gap: 10px;
  }
  /*the red yllow and green circles*/
  .r,
  .y,
  .g {
    height: 10px;
    aspect-ratio: 1/1;
    border-radius: 100%;
  }
  
  .r {
    background-color: red;
  }
  
  .y {
    background-color: yellow;
  }
  
  .g {
    background-color: #0f0;
  }
  
  .copy {
    fill:rgba(0,0,0,0.5);
    height: 40%;
  }
  
  code {
    padding: 0px 20px;
    font-size: 0.96rem;
    white-space: pre-wrap;
    overflow-y: hidden;
    overflow-x:auto;
    color:rgba(0,0,0,0.6);
      overflow-x:hidden;
  }
  
.spann {
    color: rgba(200, 200, 0, 0.948);
    font-weight: 10;
  }
  
  .select-none {
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
  }
  /*scrollbar settings*/
  /* width */
  ::-webkit-scrollbar {
  height: 3px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background:var(--primary-color); 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background:rgba(255,255,255,0.4); 
    border-radius:10px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }